* {
  margin: 0;
  padding: 0;
  overflow: visible;
}

.container {
  padding-top: 184px;
}

.gradient {
  background: linear-gradient(to top right, #267ebe, #6dd5ed);
}

.main {
  width: 1200px;
  margin: 0 auto;
}

.main_colortext {
  color: #ffffff;
  margin-bottom: 80px;
  font-size: 60px;
}

.clearfix::before,
.clearfix::after {
  content: '';
  display: table;
  clear: both;
}

.feedback_card {
  float: left;
  width: 372px;
  height: 510px;
  background-color: #ffffff;
  border-radius: 10px;
  margin-bottom: 45px;
}

.feedback_card + .feedback_card {
  margin-left: 20px;
}

.feedback_card:nth-child(4) {
  margin-left: 0;
}

.feedback_card_face {
  margin: 68px 0 0 28px;
}

.feedback_card_text {
  margin-left: 33px;
}

.feedback_card_text1_1 {
  margin: 24px 40px 0 35px;
  font-weight: 500;
  margin-bottom: 36px;
}

.feedback_card_text1_2 {
  margin-left: 33px;
}